<template>
  <div class="submit-form">
    <div v-if="!submitted">
      <h4>Add Product</h4>
      <div class="form-group">
        <label for="name">Name<span style="color: red"> *</span></label>
        <input
          type="text"
          class="form-control"
          id="name"
          required
          v-model="product.name"
          name="name"
        />
      </div>

      <div class="form-group">
        <label for="category">Category<span style="color: red"> *</span></label>
        <br />
        <select
          v-model="selected"
          class="form-control"
          @change="onChangeCategory"
          required
        >
          <option disabled>Choose Category</option>
          <option
            v-for="(key, index) in category"
            :key="key"
            v-bind:value="category[index].value"
          >
            {{ category[index].value }}
          </option>
        </select>
      </div>
      <div class="form-group">
        <label for="Detail">Detail</label>
        <textarea
          id="detail"
          class="form-control"
          v-model="product.detail"
          name="detail"
          style="resize: none"
          rows="3"
        ></textarea>
      </div>
      <div class="form-group">
        <label for="material">Material</label>
        <input
          class="form-control"
          id="material"
          v-model="product.material"
          name="material"
        />
      </div>

      <div class="form-group">
        <label for="size">Size</label>
        <input
          class="form-control"
          id="size"
          type="number"
          min="0"
          step="1"
          placeholder="0"
          v-model="product.size"
          name="size"
        />
      </div>
      <div class="form-group">
        <label for="ar_images">Ar Images</label>
        <div
          :class="[
            preview_ar_image ? 'custom-file has-image-preview' : 'custom-file',
          ]"
        >
          <div class="custom-file-ar row">
            <div class="custom-file col-10">
              <input
                type="file"
                accept="image/*"
                class="custom-file-input"
                id="ar_images"
                aria-describedby="inputGroupFileAddon01"
                name="ar_images"
                @change="onFileChangeArImage"
              />
              <label
                class="custom-file-label has-image-preview"
                for="ar_images"
                v-if="preview_ar_image"
              >
                <img class="preview" :src="preview_ar_image" />
              </label>
              <label class="custom-file-label" for="ar_images" v-else
                >Choose file</label
              >
            </div>
            <div class="remove-ar-images">
              <span
                class="remove-ar-images-input text-danger ml-3 mt-2"
                type="button"
                v-on:click="removeArImages(index - 1)"
                >X</span
              >
            </div>
          </div>
        </div>
      </div>

      <div class="form-group">
        <div class="form-group">
          <label for="inner_radius">Inner radius</label>
          <input
            type="number"
            class="form-control"
            id="inner_radius"
            min="0"
            step="1"
            v-model="product.ArImages[0].inner_radius"
            name="inner_radius"
          />
        </div>
      </div>

      <div class="form-group">
        <label for="images">Images</label>
        <div v-for="index in 5" :key="index">
          <div
            style="margin-top: 8px"
            :class="[
              preview_images[index - 1].url != ''
                ? 'input-group has-image-preview'
                : 'input-group',
            ]"
          >
            <div class="input-group-prepend">
              <span class="input-group-text">{{ index }}</span>
            </div>
            <div class="custom-file col-10">
              <input
                type="file"
                accept="image/*"
                class="custom-file-input"
                :id="'images' + index"
                aria-describedby="inputGroupFileAddon01"
                :name="'images' + index"
                @change="onFileChangeImages"
              />
              <label
                class="custom-file-label has-image-preview"
                :for="'images' + index"
                v-if="preview_images[index - 1].url != ''"
              >
                <img class="preview" :src="preview_images[index - 1].url" />
              </label>
              <label class="custom-file-label" :for="'images' + index" v-else
                >Choose file</label
              >
              <div class="remove-images col-1">
                <br />
                <span
                  class="remove-images-input text-danger"
                  :for="'removeImages' + index"
                  type="button"
                  v-on:click="removeImages(index - 1)"
                  >x</span
                >
                <br />
              </div>
            </div>
          </div>
        </div>
      </div>

      <button @click="saveProduct" class="btn btn-success">Submit</button>
      <p>{{ message }}</p>
    </div>

    <div v-else>
      <h4>You submitted successfully!</h4>
      <button class="btn btn-success" @click="newProduct">Add</button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import Product from "@/types/Product";
import Category from "@/types/Category";
import ResponseData from "@/types/ResponseData";

export default defineComponent({
  name: "add-product",
  data() {
    return {
      selected: "Choose Category",
      message: "",
      preview_ar_image: "",
      preview_images: [
        {
          image_id: 0,
          url: "",
        },
        {
          image_id: 1,
          url: "",
        },
        {
          image_id: 2,
          url: "",
        },
        {
          image_id: 3,
          url: "",
        },
        {
          image_id: 4,
          url: "",
        },
      ],
      product: {
        _id: "",
        name: "",
        detail: "",
        ArImages: [
          {
            inner_radius: 0,
            url: "",
          },
        ],
        category: "",
        images: [
          {
            image_id: 0,
            url: "",
          },
        ],
        material: "",
        size: 0,
      } as Product,
      category: [
        {
          _id: "",
          value: "",
          text: "",
          image: "",
        },
      ] as Category[],
      submitted: false,
      images: [{}] as Blob[],
      ArImages: [{}] as Blob[],
    };
  },
  methods: {
    retrieveCategory() {
      DataService.getAllCategory()
        .then((response: ResponseData) => {
          this.category = response.data.category;
          console.log(this.category);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    saveProduct() {
      if (!this.product.name || !this.product.detail) {
        this.message = "Please input Name and Category.";
        return;
      }
      let data = {
        name: this.product.name,
        detail: this.product.detail,
        category: this.product.category,
        material: this.product.material,
        size: Number(this.product.size),
      };
      console.log(data);

      DataService.create(data)
        .then((response: ResponseData) => {
          this.product._id = response.data as string;
          console.log(response.data);
          DataService.uploadImages(this.product._id, this.images)
            .then((response: ResponseData) => {
              console.log(response.data);
              DataService.uploadArImage(
                this.product._id as string,
                this.ArImages,
                this.product.ArImages
                  ? this.product.ArImages[0].inner_radius
                  : null
              )
                .then((response: ResponseData) => {
                  console.log(response.data);
                  this.submitted = true;
                })
                .catch((e: Error) => {
                  console.log(e);
                });
            })
            .catch((e: Error) => {
              console.log(e);
            });
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    newProduct() {
      this.submitted = false;
      this.product = {} as Product;
    },

    onFileChangeArImage: function (event: Event) {
      var input = event.target as HTMLInputElement;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.preview_ar_image = reader.result as string;
          input.files ? (this.ArImages[0] = input.files[0]) : null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },

    onFileChangeImages: function (event: Event) {
      var input = event.target as HTMLInputElement;
      var index = parseInt(input.name.slice(6, 7)) - 1;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.preview_images[index].url = reader.result as string;
          input.files ? (this.images[index] = input.files[0]) : null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },

    onChangeCategory: function (e: Event) {
      var input = e.target as HTMLSelectElement;
      if (input.options.selectedIndex > -1) {
        const selectedCategory =
          input.options[input.options.selectedIndex].value;
        this.product.category = selectedCategory;
      }
    },
    removeImages: function (index: number) {
      this.preview_images[index].url = "";
      index++;
      var imagesInput = document.getElementById(
        "images" + index.toString()
      ) as HTMLInputElement;
      imagesInput.value = "";
      console.log(index);
    },
    removeArImages() {
      this.preview_ar_image = "";
      var imageInput = document.getElementById("ar_images") as HTMLInputElement;
      imageInput.value = "";
    },
  },
  mounted() {
    this.retrieveCategory();
  },
});
</script>


<style src="../../css/style.css"></style>
