
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import Product from "@/types/Product";
import Category from "@/types/Category";
import ResponseData from "@/types/ResponseData";

export default defineComponent({
  name: "add-product",
  data() {
    return {
      selected: "Choose Category",
      message: "",
      preview_ar_image: "",
      preview_images: [
        {
          image_id: 0,
          url: "",
        },
        {
          image_id: 1,
          url: "",
        },
        {
          image_id: 2,
          url: "",
        },
        {
          image_id: 3,
          url: "",
        },
        {
          image_id: 4,
          url: "",
        },
      ],
      product: {
        _id: "",
        name: "",
        detail: "",
        ArImages: [
          {
            inner_radius: 0,
            url: "",
          },
        ],
        category: "",
        images: [
          {
            image_id: 0,
            url: "",
          },
        ],
        material: "",
        size: 0,
      } as Product,
      category: [
        {
          _id: "",
          value: "",
          text: "",
          image: "",
        },
      ] as Category[],
      submitted: false,
      images: [{}] as Blob[],
      ArImages: [{}] as Blob[],
    };
  },
  methods: {
    retrieveCategory() {
      DataService.getAllCategory()
        .then((response: ResponseData) => {
          this.category = response.data.category;
          console.log(this.category);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    saveProduct() {
      if (!this.product.name || !this.product.detail) {
        this.message = "Please input Name and Category.";
        return;
      }
      let data = {
        name: this.product.name,
        detail: this.product.detail,
        category: this.product.category,
        material: this.product.material,
        size: Number(this.product.size),
      };
      console.log(data);

      DataService.create(data)
        .then((response: ResponseData) => {
          this.product._id = response.data as string;
          console.log(response.data);
          DataService.uploadImages(this.product._id, this.images)
            .then((response: ResponseData) => {
              console.log(response.data);
              DataService.uploadArImage(
                this.product._id as string,
                this.ArImages,
                this.product.ArImages
                  ? this.product.ArImages[0].inner_radius
                  : null
              )
                .then((response: ResponseData) => {
                  console.log(response.data);
                  this.submitted = true;
                })
                .catch((e: Error) => {
                  console.log(e);
                });
            })
            .catch((e: Error) => {
              console.log(e);
            });
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    newProduct() {
      this.submitted = false;
      this.product = {} as Product;
    },

    onFileChangeArImage: function (event: Event) {
      var input = event.target as HTMLInputElement;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.preview_ar_image = reader.result as string;
          input.files ? (this.ArImages[0] = input.files[0]) : null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },

    onFileChangeImages: function (event: Event) {
      var input = event.target as HTMLInputElement;
      var index = parseInt(input.name.slice(6, 7)) - 1;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.preview_images[index].url = reader.result as string;
          input.files ? (this.images[index] = input.files[0]) : null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },

    onChangeCategory: function (e: Event) {
      var input = e.target as HTMLSelectElement;
      if (input.options.selectedIndex > -1) {
        const selectedCategory =
          input.options[input.options.selectedIndex].value;
        this.product.category = selectedCategory;
      }
    },
    removeImages: function (index: number) {
      this.preview_images[index].url = "";
      index++;
      var imagesInput = document.getElementById(
        "images" + index.toString()
      ) as HTMLInputElement;
      imagesInput.value = "";
      console.log(index);
    },
    removeArImages() {
      this.preview_ar_image = "";
      var imageInput = document.getElementById("ar_images") as HTMLInputElement;
      imageInput.value = "";
    },
  },
  mounted() {
    this.retrieveCategory();
  },
});
